@import url(https://fonts.googleapis.com/css?family=Pacifico&display=swap);
@import url(//db.onlinewebfonts.com/c/b593a614952261da06c53a02d1954f44?family=SEGA+LOGO+FONT);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-gradient(linear,right top, left top,from(rgb(220, 241, 31,1)), to(rgba(0,128,128,1)));
  background: -webkit-linear-gradient(right,rgb(220, 241, 31,1) 0%, rgba(0,128,128,1) 100%);
  background: linear-gradient(to left,rgb(220, 241, 31,1) 0%, rgba(0,128,128,1) 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


h1{
  font-family: 'SEGA LOGO FONT';
  font-weight: 200;
  color: #0ccaff;
}
